const ajaxGetMoreVideo = () => {
		$(document).on("click", ".btn__seeMore h4" , function(e:any) {
			e.preventDefault();
			const _thisBtn = $(this).parent()
			const url = _thisBtn.attr("data-url");
			$.ajax({
				type: "get",
				url: url,
				processData: false,
				contentType: false,
				beforeSend: function () {
				},
				success: function (res: any) {
					_thisBtn.remove();
					_thisBtn.parent().append(`${res}`)
				},
			})
		})
};

