const addIdPopup = () => {
	const listItem = document.querySelectorAll(".gallery__item");
	for (let i = 0; i < listItem.length; i++) {
		$(".item__img")
			.eq(i)
			.attr("data-fancybox", "Images-" + i);
		$(".item__img")
			.eq(i)
			.children(".listImg__gallery")
			.children("a")
			.attr("data-fancybox", "Images-" + i);
	}
	$(".gallery__item").click(function (e: any) {
		var p = $(this)
			.children(".item__txt")
			.children(".txt")
			.children("p")
			.text();
		$(this).children(".item__img").attr("data-caption", p);
	});
};

const showVideo = () => {
	if(document.querySelector(".Introduce")) {
		var player:any;
		$(document).on("click", ".video__item" , function(e:any) {
			e.preventDefault();
			$(".video__item-preview").html(`${$(this).html()}`);
			

		})
		$(".video__item").first().trigger("click");

		$(document).on("click", ".video__item-preview .videoItem" , function(e:any) {
			const videoId = $(this)[0].querySelector(".youtube-api").getAttribute("data-url")
			e.preventDefault();
			var player :any;
	
			(<any>window).YT.ready(function() {
				player = new (<any>window).YT.Player("YTB", {
					height: '100%',
					width: '100%',
					videoId: videoId,
					events: {
						'onReady': onPlayerReady,
						'onStateChange': onPlayerStateChange
					},
					playerVars: {
						// 'rel': 0,
						// 'showinfo': 0,
						controls: 1,
						loop: 0,
						color: "white",
						rel: 0,
					},
				});
				function onPlayerReady(event: any) {
					event.target.playVideo();
				}
				var done = false;
				
				function onPlayerStateChange(event :any) {
					if (event.data == YT.PlayerState.PLAYING && !done) {
						done = true;
					}
				}
				
				function stopVideo() {
					player.stopVideo();
				}
			})
			if($(this).find(".videoItem__thumbnail")[0].classList.contains("hide")) {
				$(this).find(".videoItem__thumbnail")[0].classList.remove("hide")
			} else {
				$(this).find(".videoItem__thumbnail")[0].classList.add("hide")
			}
		})
	}
};
//init submenu
const initClassSubMenu = () => {
	const header = document.querySelector("header");
	const items__MainMenu = document.querySelectorAll(
		".header__nav--bottom ul li",
	);
	// ADD LOADING HEADER
	const init = () => {
		return new Promise<void>((resolve, reject) => {
			items__MainMenu.forEach((item) => {
				const isHaveSub = item.querySelectorAll("ul");
				// CHECK MAIN MENU IS HAVE SUB ???
				if (isHaveSub.length > 0) {
					// ADD CLASS IS HAVE SUB
					item.classList.add("ishavesubmenu");
					// ADD CLASS LIST MENU LV1
					isHaveSub.forEach((item) => {
						item.classList.add("navBar--lv1");
					});
					// ADD CLASS ITEM MENU LV1
					const items__MenuLv1 = item.querySelectorAll("li");
					items__MenuLv1.forEach((item) => {
						item.classList.add("navBar__item--lv1");
					});
				}
			});
			resolve();
		});
	};
	init().then(() => {
		setTimeout(() => {
			header.removeAttribute("loading");
			header.setAttribute("loaded", "");
		}, 300);
	});
};
//active menu
const activeMenu = () => {
	const link = window.location.href;
	const listSubLink = document.querySelectorAll(
		".header__nav--bottom ul.navBar--lv1 li",
	);
	listSubLink.forEach((item) => {
		const href = item.querySelector("a").getAttribute("href");
		if (link.includes(href)) {
			item.classList.add("active");
			item.parentElement.parentElement.classList.add("active");
		}
	});
	const listNavLink = document.querySelectorAll(".header__nav--bottom ul li");
	listNavLink.forEach((item) => {
		const href = item.querySelector("a").getAttribute("href");
		if (link.includes(href)) {
			item.classList.add("active");
		}
	});
};
const ajaxFormContact = () => {
	$(".contact__form .btn__submit--contact").on("click", function (e: any) {
		e.preventDefault();
		const _thisBtn = $(this);
		const url = _thisBtn.attr("data-url");
		const formData = new FormData();
		const nameText = $(".contact__form .form__group textarea").attr(
			"name",
		);
		const valText = $(".contact__form .form__group textarea").val();
		console.log(nameText , valText);
		
		formData.append(nameText, valText);
		$(".contact__form .form__group input").each(function () {
			const name = $(this).attr("name");
			const value = $(this).val();
			formData.append(name, value);
		});

		const responserRecaptcha: HTMLInputElement = document.querySelector(".g-recaptcha")
		const valueRecaptcha = responserRecaptcha.value;
		const nameRecaptcha = responserRecaptcha.getAttribute("name");
		formData.append(nameRecaptcha , valueRecaptcha)
		if ($(".contact__form .form form").valid() === true) {
			$.ajax({
				url: url,
				type: "post",
				data: formData,
				processData: false,
				contentType: false,
				beforeSend: function () {
					_thisBtn.attr("disabled", "disabled");
				},
				success: function (res: any) {
					alert(`${res.Message}`);
					window.location.reload();
					_thisBtn.removeAttr("disabled");
				},
			});
		} 
	});
};
