const nextStepOnPay = () =>{
	const btnStep1 = document.querySelector(".btn__next-pay.step-1");
	const btnStep2 = document.querySelector(".btn__next-pay.step-2");
	if(btnStep1){
		$(".btn__next-pay.step-1").on("click" , function(e:any) {
			const _thisBtn = $(this);
			const href = _thisBtn.attr("data-next");
			const url = _thisBtn.attr("data-url");
			const formData = new FormData();
			$(".location__input input").each(function () {
				const name = $(this).attr("name");
				const value = $(this).val();
				formData.append(name, value);
			});
			$(".location__input select").each(function () {
				const name = $(this).attr("name");
				const value = $(this).val();
				formData.append(name, value);
			});
			if ($(".pay__location form").valid() === true) {
				$.ajax({
					url: url,
					type: "post",
					data: formData,
					processData: false,
					contentType: false,
					beforeSend: function () {
						_thisBtn.attr("disabled", "disabled");
					},
					success: function (res: any) {
						if(res.Code==200){
							window.location.href = href;
							_thisBtn.removeAttr("disabled");
						}else{
							alert(`${res.Message}`);
						}
					},
				});
			}
		})
	}
	if(btnStep2){
		$(".btn__next-pay.step-2").on("click" , function(e:any) {
			e.preventDefault();
			const _thisBtn = $(this);
			const href = _thisBtn.attr("data-next");
			const url = _thisBtn.attr("data-url");
			const formData = new FormData();
			$(".mtItem__radio input:checked").each(function () {
				const name = $(this).attr("name");
				const value = $(this).val();
				formData.append(name, value);
			});
			if ($(".pay__method form").valid() === true) {
				$.ajax({
					url: url,
					type: "post",
					data: formData,
					processData: false,
					contentType: false,
					beforeSend: function () {
					},
					success: function (res: any) {
						window.location.href = res.Result;
					},
				});
			}
		})
	}
}

const updateAccount = () => {
	if(document.querySelector(".account-manage")) {
		document.querySelector(".btn-cancle").addEventListener("click", (e:any) => {
			window.location.reload();
		})
		document.querySelector(".btn-save").addEventListener("click", (e:any) => {
			e.preventDefault();
		
			const formAccount = new FormData();
			document.querySelectorAll("#self-info form .form-input input").forEach((item:any) => {
				const name = item.getAttribute("name")
				const value = item.value;
				formAccount.append(name,value)
			})
			document.querySelectorAll("#self-info form .form-input select").forEach((item:any) => {
				const name = item.getAttribute("name")
				const value = item.value
				formAccount.append(name,value)
			})
			document.querySelectorAll("#self-info form .form-check input:checked").forEach((item:any) => {
				const name = item.getAttribute("name")
				const value = item.value
				formAccount.append(name,value)
			})
			const url = e.target.getAttribute("data-url")
			if($("#self-info form").valid() == true) {
				Axios.interceptors.request.use( (config) => {
					$(e.target).attr("disabled", "disabled");
					return config;
				})
				Axios.post(`${url}` , formAccount,{headers: {'Content-Type': 'application/json'}}).then((res:any) => {
					if(res.data.Code == 200) {
						window.location.reload();
					} else {
						console.log(res.data.Message);
						$(e.target).removeAttr("disabled");
					}
				}).catch((err: any) => {
					console.log(err);
					$(e.target).removeAttr("disabled");
				})
			}
		})
	}
}

const subscribeFooter = () => {
document.querySelector(".footer__subscribe form button")
	.addEventListener("click" , (e:any) => {
		e.preventDefault();
		const formdata = new FormData();
		document.querySelectorAll(".footer__subscribe form input").forEach((item:any) => {
			const name = item.getAttribute("name");
			const value = item.value;
			formdata.append(name , value);
		})
		
		const url = $(e.target).attr("data-url");
		if($(".footer__subscribe form").valid() == true) {
			Axios.interceptors.request.use((config) => {
				$(e.target).attr("disabled", "disabled");
				return config;
			})
			Axios.post(url, formdata, {headers : {'Content-Type': 'application/json'}}).then((res:any) => {
				if(res.data.Code==200) {
					alert(res.data.Message)
					$(e.target).removeAttr("disabled");
					window.location.reload();
				}
				if(res.data.Code == 400) {
					console.log(res.data.Message);
					$(e.target).removeAttr("disabled");
				}
			}).then(res => {
				$(e.target).removeAttr("disabled");
			}).catch(err => {
				console.log(err);
				$(e.target).removeAttr("disabled");
			})
		}
	})
}

