// AJAX COMMENT
function AjaxComment() {
	$("body").on("click", ".submit.comment", function (e:any) {
		e.preventDefault();
		const isLogin = $('.account-item').attr('data-login');
		if (isLogin == "true") {
			var product_ID = $(this).siblings("input[name='content-comment']").attr("product-id");
			var newCommentContent = $(this).siblings("input[name='content-comment']").val();
			var rate = document.querySelector(".list-step-comment .rate").getAttribute("data-rate");
				$.ajax({
					type: "post",
					url: "/binh-luan",
					data: {
						Id: product_ID,
						Content: newCommentContent,
						vote: rate
					},
					success: function (res:any) {
						if (res.Code == 200) {
							location.reload();
						} else {
							alert(res.Message);
						}
					},
				});
			} else {
			$.fancybox.open({
				src: '#login',
				type : "inline",
			});
		}
	});
}

const addCityInfo = () => {
	if(document.querySelector("#ShippingCitySelectedValue")) {

		//city
		var _this = $(".reveive")
		var id = ($.isNumeric($(_this).attr("data-city")) ? Number($(_this).attr("data-city")) : 0);
		// Init district id
		// $("#ShippingCitySelectedValue").attr("data-district", ($.isNumeric($(_this).attr("data-district")) ? Number($(_this).attr("data-district")) : 0));
		$.ajax({
			// url: '/get-payment-city',
			url: `/get-payment-city?id=${id}`,
			type: 'GET',
			contenttype: "application/json; charset=utf-8",
			beforeSend: function() {
				$('#ShippingCitySelectedValue').attr("disabled", true);
				$('#ShippingCitySelectedValue').html('<option>Đang tải...</option>')
			},
			success: function(response:any) {
				$('#ShippingCitySelectedValue').attr("disabled", false);
				$('#ShippingCitySelectedValue').children().remove();
				$.each(response, function(i:any, item:any) {
					$("#ShippingCitySelectedValue").append($('<option></option>').val(item.Value).html(item.Text).attr('selected', item.Selected));
				})

				$("#ShippingCitySelectedValue").trigger("change");
			},
			failure: function(errmsg:any) {
				alert(errmsg);
			}
		});

		// district
		var idDistrict = ($.isNumeric($(_this).attr("data-district")) ? Number($(_this).attr("data-district")) : 0);
		if(idDistrict != 0) {
			$.ajax({
				url: `/get-payment-district?cityId=${id}&id=${idDistrict}`,
				type: 'GET',
				contenttype: "application/json; charset=utf-8",
				beforeSend: function() {
					$('#ShippingDistrictSelectedValue').attr("disabled", true);
					$('#ShippingDistrictSelectedValue').html('<option>Đang tải...</option>')
				},
				success: function(response:any) {
					$('#ShippingDistrictSelectedValue').attr("disabled", false);
					$('#ShippingDistrictSelectedValue').children().remove();
					$.each(response, function(i:any, item:any) {
						$("#ShippingDistrictSelectedValue").append($('<option></option>').val(item.Value).html(item.Text).attr('selected', item.Selected));
					})
				},
				failure: function(errmsg:any) {
					alert(errmsg);
				}
			});
		}

		$("#ShippingCitySelectedValue").on("change" , function(e:any) {
			// district
			$.ajax({
				url: `/get-payment-district?cityId=${$(this).val()}`,
				type: 'GET',
				contenttype: "application/json; charset=utf-8",
				beforeSend: function() {
					$('#ShippingDistrictSelectedValue').attr("disabled", true);
					$('#ShippingDistrictSelectedValue').html('<option>Đang tải...</option>')
				},
				success: function(response:any) {
					$('#ShippingDistrictSelectedValue').attr("disabled", false);
					$('#ShippingDistrictSelectedValue').children().remove();
					$.each(response, function(i:any, item:any) {
						$("#ShippingDistrictSelectedValue").append($('<option></option>').val(item.Value).html(item.Text).attr('selected', item.Selected));
					})
				},
				failure: function(errmsg:any) {
					alert(errmsg);
				}
			});
		})
	}
}

// ĐÁNH GIÁ SẢN PHẨM KHI BÌNH LUẬN
const clickRating = () => {
	$(".list-step .rate").each(function () {
		var _this = $(this);
		if (_this.find(".img-star").length > 0) {
			_this.find(".img-star .star").on("click", function (e:any) {
				let offsetLeft = _this.find(".img-star .star").offset().left;
				let width = _this.find(".img-star .star").width();
				let positionClicked = e.pageX - Math.round(offsetLeft);
				let starRatedWidth = Math.floor((positionClicked / width) * 100);

				if (starRatedWidth >= 80) {
					_this.find(".img-star .star-rated").width("100%");
					$(".rate").attr("data-rate", 5);
				} else if (starRatedWidth >= 60) {
					_this.find(".img-star .star-rated").width("80%");
					$(".rate").attr("data-rate", 4);
				} else if (starRatedWidth >= 40) {
					_this.find(".img-star .star-rated").width("60%");
					$(".rate").attr("data-rate", 3);
				} else if (starRatedWidth >= 20) {
					_this.find(".img-star .star-rated").width("40%");
					$(".rate").attr("data-rate", 2);
				} else {
					_this.find(".img-star .star-rated").width("20%");
					$(".rate").attr("data-rate", 1);
				}
			});
		}
	});
}

const showRating = () => {
	$(".block-comment .rate[data-rate]").each(function () {
		var numberStart = $(this).attr("data-rate");

		if (numberStart == 5) {
			$(this).find(".img-star .star-rated").width("100%");
		} else if (numberStart == 4) {
			$(this).find(".img-star .star-rated").width("80%");
		} else if (numberStart == 3) {
			$(this).find(".img-star .star-rated").width("60%");
		} else if (numberStart == 2) {
			$(this).find(".img-star .star-rated").width("40%");
		} else if (numberStart == 1) {
			$(this).find(".img-star .star-rated").width("20%");
		}
	});

	$(".info-summary .rate[data-rate]").each(function () {
		var numberStart = $(this).attr("data-rate");

		if (numberStart == 5) {
			$(this).find(".img-star .star-rated").width("100%");
		} else if (numberStart == 4) {
			$(this).find(".img-star .star-rated").width("80%");
		} else if (numberStart == 3) {
			$(this).find(".img-star .star-rated").width("60%");
		} else if (numberStart == 2) {
			$(this).find(".img-star .star-rated").width("40%");
		} else if (numberStart == 1) {
			$(this).find(".img-star .star-rated").width("20%");
		}
	});

	$(".block-rating .rate[data-rate]").each(function () {
		var numberStart = $(this).attr("data-rate");

		if (numberStart == 5) {
			$(this).find(".img-star .star-rated").width("100%");
		} else if (numberStart == 4) {
			$(this).find(".img-star .star-rated").width("80%");
		} else if (numberStart == 3) {
			$(this).find(".img-star .star-rated").width("60%");
		} else if (numberStart == 2) {
			$(this).find(".img-star .star-rated").width("40%");
		} else if (numberStart == 1) {
			$(this).find(".img-star .star-rated").width("20%");
		}
	});
}

// LẤY % SAO
const getDataBar = () => {
	let dataBarNodeList = document.querySelectorAll(
		".statistical-rating .middle .bar"
	);
	Array.prototype.forEach.call(dataBarNodeList, function (e:HTMLElement, index:number) {
		const data = e.getAttribute("data-bar");
		e.style.width = data + "%";
	});
}

const AjaxDeteleComment = () => {
	$("body").on("click", ".button-delete-comment", function (e:MouseEvent) {
		e.preventDefault();
		
		const isLogin: any = document
			.querySelector<HTMLElement>(".account-item")
			.getAttribute("data-login")
		if (isLogin == "true") {
			var deleteInfo = $(this)
				.parents(".main-comment[comment-id]")
				.attr("comment-id");
			var _thisCommentDelete = $(this);
			$.ajax({
				type: "get",
				url: `/xoa-binhluan?id=${deleteInfo}`,
				success: function (res:any) {
					if (res.Code == 200) {
						_thisCommentDelete.find("span").html(res.Message).addClass("deleted");
					} else {
						alert(res.Message);
					}
				},
			});
		} else {
			$.fancybox.open({
				src: '#login',
				// opts: {
				// 	afterShow: function (instance, current) { }
				// }
			});
		}
	});
}

